import $ from 'jquery'

//------------------------
// Floating Labels
//------------------------
export function floatingLabel() {
  'use strict'

  let floatingLabelSelector,
      floatingLabelToggle

  floatingLabelSelector = $('.floating-label > .form-control')

  floatingLabelToggle = function(el) {
    let $el = $(el)
    if ($el.val() === '') {
      $el.siblings('label').toggleClass('floating')
    }
  }

  floatingLabelSelector.on('focus', function() {
    floatingLabelToggle(this)
  }).on('blur', function() {
    floatingLabelToggle(this)
  })

}