import $ from 'jquery'

//------------------------
// Colored popovers
//------------------------
export function popoverColored() {
  'use strict'

  $('[data-toggle="popover"]').each(function() {

    let popoverColor = $(this).data('color'),
        popoverClass,
        popoverTemplate

    if (popoverColor) {
      popoverClass = `popover-${popoverColor}`
      popoverTemplate = `<div class="popover ${popoverClass}" role="tooltip"><div class="arrow"></div><h3 class="popover-header"></h3><div class="popover-body"></div></div>`
      
      $(this).popover({ template: popoverTemplate })
    }
  })
}