import $ from 'jquery'

//------------------------
// Colored Tooltips
//------------------------
export function tooltipColored() {
  'use strict'

  $('[data-toggle="tooltip"]').each(function() {

    let tooltipColor = $(this).data('color'),
        tooltipClass,
        tooltipTemplate

    if (tooltipColor) {
      tooltipClass = `tooltip-${tooltipColor}`
      tooltipTemplate = `<div class='tooltip ${tooltipClass}' role='tooltip'><div class='arrow'></div><div class='tooltip-inner'></div></div>`
     
      $(this).tooltip({ template: tooltipTemplate })
    }
  })
}