import $ from 'jquery'

const NAME                = 'togglePassword';
const VERSION             = '0.0.1';
const DATA_KEY            = 'bend.togglePassword'
const EVENT_KEY           = `.${DATA_KEY}`
const DATA_API_KEY        = '.data-api'
const JQUERY_NO_CONFLICT  = $.fn[NAME]

const ClassName = {
  BUTTON              : 'btn'
}
const Selector = {
  DATA_TOGGLE        : '[data-bend-toggle="password"]',
  BUTTON             : '.btn'
}

const Event = {
  HIDE                : `hide${EVENT_KEY}`,
  HIDDEN              : `hidden${EVENT_KEY}`,
  SHOW                : `show${EVENT_KEY}`,
  SHOWN               : `shown${EVENT_KEY}`,
  CLICK_DATA_API      : `click${EVENT_KEY}${DATA_API_KEY}`
}

const InputTypes = {
  TEXT                : 'text',
  PASSWORD            : 'password'
}

const Default = {
  iconHidden          : '<i class="far fa-eye-slash"></i>',
  // iconHidden          : 'Show',
  iconShown           : '<i class="far fa-eye"></i>',
  // iconShown           : 'Hide',
  tooltip             : true,
  tooltipHiddenTitle  : 'Show password',
  tooltipShownTitle   : 'Hide password'
}

class TogglePassword {
  constructor (element, config) {
    this._element = element;
    this.$element = $(this._element);

    this._config   = this._getConfig(config);

    this.init.call(this);
  }

  static get VERSION() {
    return VERSION
  }

  static get Default() {
    return Default
  }

  init() {
    this.passwordInput = this._getTargetElement.call(this);    
    this._appendButtonIcon(this._config.iconHidden);
    this._initiateTooltip.call(this);
  }
  
  toggle() {
    let inputType = this.passwordInput.attr('type');

    if (inputType === InputTypes.PASSWORD) {
      this.show();
    } else if (inputType === InputTypes.TEXT) {
      this.hide();
    }
  }

  show() {
    this.$element.trigger(Event.SHOW);
    this._toggleTargetInputType(InputTypes.TEXT);
    this._appendButtonIcon(this._config.iconShown);
    if(this._config.tooltip === true) {
      this._updateTooltip(this._config.tooltipShownTitle);
    }
    this.$element.trigger(Event.SHOWN);
  }

  hide() {
    this.$element.trigger(Event.HIDE);
    this._toggleTargetInputType(InputTypes.PASSWORD);
    this._appendButtonIcon(this._config.iconHidden);
    if(this._config.tooltip === true) {
      this._updateTooltip(this._config.tooltipHiddenTitle);
    }
    this.$element.trigger(Event.HIDDEN);
  }

  dispose() {
    $.removeData(this._element, DATA_KEY)
    this._element = null
  }

  _toggleTargetInputType(type) {
    this.passwordInput.attr('type', type);
  }

  _appendButtonIcon(icon) {
    this.$element.html('').append(icon);
  }

  _initiateTooltip() {
    if(this._config.tooltip === true) {
      this.$element.tooltip({
        title: this._config.tooltipHiddenTitle
      });    
    }
  }

  _updateTooltip(title) {
    this.$element.tooltip('hide').attr('data-original-title', title).tooltip('update');
    if ( this.$element.is(':hover, :focus') ) {
      this.$element.tooltip('show');
    }
  }

  _getTargetElement() {
    let dataTarget = this.$element.data('bend-target');
    return $(dataTarget);
  }

  _getConfig(config) {
    config = {
      ...this.constructor.Default,
      ...$(this._element).data(),
      ...config
    }

    return config
  }

  static _jQueryInterface(config) {
    return this.each(function() {

      let data = $(this).data(DATA_KEY)
      const _config = typeof config === 'object' && config

      if (!data) {
        data = new TogglePassword(this, _config)
        $(this).data(DATA_KEY, data)
      }

      if (typeof config === 'string') {
        if (typeof data[config] === 'undefined') {
          throw new TypeError(`No method named "${config}"`)
        }
        data[config]()
      }
    })
  }
}

$(document).on(
  Event.CLICK_DATA_API,
  Selector.DATA_TOGGLE,
  (event) => {
    event.preventDefault()

    let button = event.target

    if (!$(button).hasClass(ClassName.BUTTON)) {
      button = $(button).closest(Selector.BUTTON)
    }

    TogglePassword._jQueryInterface.call($(button), 'toggle')
  }
)

$.fn[NAME] = TogglePassword._jQueryInterface
$.fn[NAME].Constructor = TogglePassword
$.fn[NAME].noConflict = () => {
  $.fn[NAME] = JQUERY_NO_CONFLICT
  return TogglePassword._jQueryInterface
}

export default TogglePassword
