import $ from 'jquery'

//------------------------
// Input Group - Focus
//------------------------
export function inputGroups() {
  'use strict'

  let inputGroupSelector,
      inputGroupToggle

  inputGroupSelector =  $('.input-group > .form-control')
  inputGroupToggle = function(el) {
    var $el = $(el)
    $el.parent('.input-group').toggleClass('focus')
  }

  inputGroupSelector.on('focus', function() {
    inputGroupToggle(this)
  }).on('blur', function() {
    inputGroupToggle(this)
  })

}
